import React from 'react';

class ProductListItem extends React.Component {
  render() {

    let formattedPrice = parseInt(this.props.price).toFixed(2);
    return (
      <div className="m-auto bg-info d-flex align-items-center ">
        <div className="bg-light">
          <hr></hr>
          <div className="d-flex row">

            {/* COL 1 - PICTURE/PRICE */}
            <span className="col-md-4 d-flex flex-column badge badge-secondary m-auto">
              <div className="d-flex">
                <div className="container-fluid" onClick={() => this.props.setView('details', this.props.id)}>
                  <img className="img-fluid" src={this.props.images[0]} alt=""/>
                </div>
              </div>

            </span>

            {/* COL 2 - DESCRIPTION | PRICE | NAME */}
            <div className="col-md-4 m-auto">
              <div className="container d-flex flex-column">

                <div className="container m-auto">
                  <h5>{this.props.name}</h5>
                </div>

                <div className="container m-auto">
                  {this.props.description}
                </div>

                <div className="container m-auto">
                  <div className="badge badge-success m-auto">
                    {/* {this.props.price} */}
                    ${formattedPrice}
                  </div>
                </div>

                <div className="container m-auto">
                  <button onClick={() => this.props.setView('details', this.props.id)}>View {this.props.name}</button>
                </div>

              </div>

            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ProductListItem;
