import React from 'react';

export default class CheckoutForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      customerName: '',
      customerCreditCard: '',
      customerShippingAddress: ''
    };
    this.handleChange = this.handleChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  handleChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }
  handleSubmit(event) {
    if (!this.state.customerName) {
      alert('please enter your name');
      event.preventDefault();
    } else if (!this.state.customerCreditCard) {
      alert('please enter your credit card information');
      event.preventDefault();
    } else if (!this.state.customerShippingAddress) {
      alert('please enter your shipping address');
      event.preventDefault();
    } else {
      this.props.placeOrder(this.state);
      event.preventDefault();
      this.props.setView('catalog');
    }

  }
  render() {
    return (
      <div className="container bg-dark">
                Customer Input Form
        <form onSubmit={this.handleSubmit}>
          <label>
                        Name:
            <input type="text" name="customerName" value={this.state.customerName} onChange={this.handleChange}/>
          </label>

          <label >
                        Credit Card:
            <input type="number" name="customerCreditCard" value={this.state.customerCreditCard} onChange={this.handleChange}/>
          </label>

          <label htmlFor="">
                        Shipping Address:
            <textarea name="customerShippingAddress" value={this.state.customerShippingAddress} cols="30" rows="10" onChange={this.handleChange}>
            </textarea>
          </label>
          <input type="submit" value="Place Order! :D"/>
        </form>
      </div>
    );
  }
}
