import React from 'react';
import Header from './header';
import ProductList from './product-list';
import ProductDetails from './product-details';
import CartSummary from './cartsummary';
import CheckoutForm from './checkoutform';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      view: {
        name: 'catalog',
        params: {}
      },
      cart: []
    };
    this.setView = this.setView.bind(this);
    this.getCartItems = this.getCartItems.bind(this);
    this.addToCart = this.addToCart.bind(this);
    this.placeOrder = this.placeOrder.bind(this);
  }
  componentDidMount() {
    this.getCartItems();
  }
  placeOrder(orderObj) {
    this.setState({ cart: [] });

    // fetch('/api/orders.php', {method: 'POST', headers:{'Content-Type': 'application/json'}, body: JSON.stringify(orderObj)})
    //   .then(res => res.json())
    //   .then(res => {
    //     console.log(res);
    //     this.setState({
    //       view: {
    //         name: 'catalog',
    //         params: {}
    //       },
    //       cart: []
    //     })
    //   })

  }
  addToCart(product) {
    fetch('/api/cart.php', { method: 'POST', headers: { 'Content-Type': 'application/json' }, body: JSON.stringify(product) })
      .then(res => res.json())
      .then(res => {
        let updatedCart = this.state.cart.concat(product);
        this.setState({
          cart: updatedCart
        });
      });
  }
  getCartItems() {
    fetch('/api/cart.php')
      .then(res => res.json())
      .then(data => {
        for (let productData in data) {
          let updatedCart = this.state.cart.concat(data[productData]);
          this.setState({ cart: updatedCart });
        }
      });
  }
  setView(name, params) {
    this.setState({
      view: {
        name: name,
        params: params
      }
    });
  }
  render() {
    const { name } = this.state.view;
    let element;
    switch (name) {
      case 'catalog':
        element = <ProductList
          setView={this.setView}
        />;
        break;
      case 'details':
        element = <ProductDetails
          setView={this.setView}
          addToCart={this.addToCart}
          cart={this.state.cart}
          currentProductId={this.state.view.params}
        />;
        break;
      case 'cart':
        element = <CartSummary
          setView={this.setView}
          cartArray={this.state.cart}
        />;
        break;
      case 'checkout':
        element = <CheckoutForm
          setView={this.setView}
          placeOrder={this.placeOrder}
        />;
        break;
    }
    return (
      <div className="container min-vw-100">
        {/* container-fluid h-100 bg-dark d-flex flex-column */}
        <Header setView={this.setView} cartItemCount={this.state.cart.length} />
        {element}
      </div>
    );
  }
}
