import React from 'react';
import CartSummaryItem from './cartsummaryitem';

export default class CartSummary extends React.Component {
  render() {
    let elementToRender;
    let totalPrice = this.props.cartArray.reduce((sum, product) => {

      sum += parseInt(product.price);
      return sum;
    }, 0);
    totalPrice = (totalPrice / 100).toFixed(2);
    if (this.props.cartArray.length === 0) {
      elementToRender =
            <div>
                Your Cart Is Too Full
            </div>;
    } else {
      elementToRender =
            <div>
              <ol>
                {this.props.cartArray.map((product, index) =>
                  <li key={index}>
                    <CartSummaryItem
                      image={product.image}
                      name={product.name}
                      price={product.price}
                      description={product.shortDescription}
                    />
                  </li>)
                }
              </ol>
            </div>;
    }
    return (
      <div className="container bg-light d-flex flex-column">
        <div className="container d-flex ">
          <button className="badge badge-pill align-self-center m-auto" onClick={() => this.props.setView('catalog')}>Back to Catalog</button>
        </div>

        <div className="container overflow-auto ">
          {elementToRender}
        </div>

        <div className="bg-dark text-light">
          <h1>
                        Total Cost: ${totalPrice}
          </h1>
        </div>

        <div className="m-auto bg-dark text-secondary">
          <button onClick={() => this.props.setView('checkout')}> Checkout :D</button>
        </div>

      </div>
    );
  }
}
