import React from 'react';

export default class ProductDetails extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      product: null
    };
  }
  componentDidMount() {
    fetch(`/api/products.php?id=${this.props.currentProductId}`)
      .then(data => data.json())
      .then(details => {
        this.setState({ product: details });
      });
  }
  render() {

    let product = this.state.product;
    if (!product) {
      return null;
    } else {
      return (
        <div className="container d-flex flex-column text-center bg-light">
          <div className="h2 bg-light ">Product Details for {this.props.currentProductId}</div>
          <div className="container bg-info m-auto">
                details - {product[0].name}
            <div className="container rounded">
              <img src={product[0].images[0]} className="img-thumbnail" alt=""/>
            </div>
            <div className="container rounded">
              <img src={product[0].images[1]} className="img-thumbnail" alt=""/>
            </div>
          </div>

          <button onClick={ () => this.props.addToCart(product[0])} className="m-auto">Add To Cart</button>
          <button onClick={ () => this.props.setView('catalog', product[0])} className="m-auto btn btn-secondary">Back to Catalog</button>
        </div>
      );
    }
  }
}
